.modal-block {
  color: #454545;
  padding: 10px 40px;
}

@media screen and (max-width: 500px) {
  .modal-block {
    padding: 10px 10px;
  }
}

.enroll-modal-box.open {
  display: block;
}

.enroll-modal-box {
  display: none;
  z-index: 9999;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  font-family: Ubuntu, sans-serif;
}

.enroll-modal {
  padding: 20px 0;
  width: 430px;
  background-color: #fff;
  border-radius: 5px;
  border-left: 20px solid;
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 100px;
}

@media screen and (max-width: 500px) {
  .enroll-modal {
    flex-grow: 1;
    border: none;
  }
}

.modal-close {
  top: 20px;
  right: 20px;
  position: absolute;
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.modal-header {
  display: -ms-flexbox;
  display: flex;
  align-items: center;
}

.modal-header-cover {
  display: block;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  -o-object-fit: cover;
     object-fit: cover;
}

.modal-header-info {
  padding: 0 20px;
}

.modal-header-title {
  font-size: 20px;
  margin-bottom: 10px;
}

.header-extra-item {
  display: -ms-inline-flexbox;
  display: inline-flex;
  height: 20px;
  line-height: 20px;
  vertical-align: middle;
  margin-right: 8px;
}

.extra-item-icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  margin-right: 5px;
}

.pre-enroll {
  flex-wrap: wrap;
  align-items: center;
  padding: 10px 20px;
  background-color: #ededee;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-around;
}

@media screen and (max-width: 500px) {
  .event-info {
    text-align: center;
    margin-bottom: 10px;
  }
}

.show-form {
  height: 40px;
  width: 120px;
}

@media screen and (max-width: 500px) {
  .show-form {
    width: 220px;
  }
}

.modal-enroll {
  display: none;
  background-color: #ededee;
}

.modal-enroll-form {
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  width: 220px;
  padding: 10px 0;
}

.enroll-field-wrap {
  position: relative;
  margin-bottom: 10px;
}

.enroll-input {
  width: 100%;
  height: 40px;
  border-radius: 3px;
  border: 1px solid #7f8285;
}

.enroll-input-field {
  padding: 9px 20px;
  background-color: transparent;
}

.enroll-field-label {
  border-radius: 3px;
  border: 1px solid #7f8285;
  color: #515b60;
  position: absolute;
  height: 30px;
  width: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 40px;
  background-color: #ededee;
  padding: 9px 20px;
}

.enroll-input:focus ~ .enroll-field-label {
  display: none;
}

.enroll-input.filled ~ .enroll-field-label {
  display: none;
}

.modal-button {
  background-color: #ff5656;
  border: none;
  color: #ffffff;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}

.enroll-status {
  text-align: center;
  padding: 20px 0;
  display: none;
}

.enroll-success {
  color: #43a200;
}

.enroll-fail {
  color: #dc3545;
}

.modal-about-tabs {
  display: -ms-flexbox;
  display: flex;
  border-bottom: 2px solid #ededee;
  margin-bottom: 5px;
}

.modal-about-tab {
  flex-grow: 1;
  transition: all 0.4s ease;
  padding: 10px;
  font-size: 13px;
  cursor: pointer;
  text-align: center;
}

.modal-about-tab.disabled {
  display: none;
  cursor: default;
  color: #929292;
}

@media screen and (max-width: 500px) {
  .modal-about-tab {
    font-size: 11px;
  }
}

.modal-about-tab.current {
  background-color: #ededee;
}

.modal-about-tab-body {
  display: none;
  padding: 10px;
}

.modal-about-tab-body.current {
  display: inline;
  display: initial;
}

.modal-teacher {
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-teacher-photo {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  -o-object-fit: cover;
     object-fit: cover;
}

.modal-teacher-info {
  padding: 0 20px;
}

.modal-teacher-heading {
  line-height: 1.2;
}

.modal-teacher-name {
  font-size: 18px;
}
.preschoolars .theme {
