* {
  box-sizing: border-box;
}

.master-classes {
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
}

.mc-cards {
  padding: 50px 0;
  justify-content: center;
  max-width: 1300px;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
}

.mc-card {
  position: relative;
  font-family: Ubuntu, sans-serif;
  width: 380px;
  margin: 5px;
  height: 380px;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 500px) {
  .mc-card {
    width: 300px;
  }
}

.mc-card .more {
  cursor: pointer;
  position: absolute;
  display: block;
  top: 20px;
  right: 15px;
  width: 115px;
  height: 42px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  color: #fff;
  border-radius: 30px;
  text-align: center;
  line-height: 42px;
  border: none;
  outline: none;
}

.mc-cover {
  display: block;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  flex: 1 1;
}

.mc-footer {
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  min-height: 140px;
}

.mc-footer .more {
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 20px;
  display: block;
  text-decoration: none;
  color: #fff;
}

.mc-date {
  padding: 15px 10px;
  width: 25%;
  text-align: center;
  font-weight: 700;
}

.mc-date .day {
  font-size: 40px;
}

@media screen and (max-width: 500px) {
  .mc-date .day {
    font-size: 30px;
  }
}

.mc-date .month {
  font-size: 28px;
}

@media screen and (max-width: 500px) {
  .mc-date .month {
    font-size: 20px;
  }
}

.mc-description {
  width: 75%;
  background-color: #323232;
  padding: 15px 20px;
}

.mc-description .title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 10px;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

@media screen and (max-width: 500px) {
  .mc-description .title {
    font-size: 16px;
  }
}

.mc-description .time {
  font-size: 15px;
  font-weight: bold;
}

.mc-description .address {
  font-size: 12px;
}
